<template>
  <div class="user-center">
    <div class="user-center-header">
      <div class="user-info">
        <div class="user-avatar"></div>
        <div class="user-phone">{{userInfo.nickname||'-'}}</div>
      </div>
      <div class="user-money">
        <div class="user-money-l">
          <div class="money">{{userInfo.rebateFee||0 | tofixedNum}}</div>
          <div class="desc">我的返利（元）</div>
        </div>
        <div class="user-money-r">
          <div class="money">{{userInfo.balanceFee||0 | tofixedNum}}</div>
          <div class="desc">我的钱包（元）</div>
        </div>
      </div>
    </div>
    <div class="user-center-body">
      <div class="tips">应用商城搜索并下载“财喵万卡”APP，可查看详细返利哦！</div>
    </div>
    <FloatTool></FloatTool>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import FloatTool from '../../components/FloatTool.vue'
import { setTimeout } from 'timers'

export default {
  data () {
    return {
      userInfo: {}
    }
  },
  filters: {
    formatPhone (val) {
      var pat = /(\d{3})\d*(\d{4})/
      var b = val.replace(pat, '$1****$2')
      return b
    },
    tofixedNum (val) {
      return val.toFixed(2)
    }
  },
  components: { FloatTool },
  methods: {
    ...mapGetters(['GETUSERINFO'])
  },
  created () {
    this.userInfo = this.GETUSERINFO()
  }
}
</script>

<style lang="scss" scoped>
  .user-center {
    width: 375px;
  }
  .user-center-header {
    height: 184px;
    background: #FD6D47 url(../../assets/images/my_bg@2x.png) center top no-repeat;
    background-size: cover;
    padding: 24px 19px 27px 19px;
  }
  .user-info {
    display: flex;
    align-items: center;
  }
  .user-avatar {
    width: 52px;
    height: 52px;
    background: url(../../assets/images/avatar_default.png);
    background-size: cover;
    margin-right: 15px;
  }
  .user-phone {
    font-family: PingFangSC-Medium;
    font-size: 18px;
    color: #FFFFFF;
    letter-spacing: 0;
  }
  .user-money {
    margin-top: 23px;
    display: flex;
    justify-content: center;
    align-items: center;
    .user-money-l, .user-money-r {
      position: relative;
      width: 50%;
      padding-left: 40px;
    }
    .user-money-l:after {
      content: '';
      width: 2px;
      height: 24px;
      background: rgba(255,255,255,0.50);
      display: block;
      position: absolute;
      right: 0;
      top: 16px;
    }
    .money {
      font-family: DINMittelschriftStd;
      font-size: 26px;
      color: #FFFFFF;
      letter-spacing: 0;
    }
    .desc {
      font-family: PingFangSC-Regular;
      font-size: 12px;
      color: #FFFFFF;
      letter-spacing: 0;
    }
  }
  .user-center-body {
    .tips {
      font-family: PingFangSC-Regular;
      font-size: 12px;
      color: #9B9B9B;
      letter-spacing: 0;
      text-align: center;
      padding-top: 18px;
      text-align: center;
    }
  }
</style>



